import { Paper, Box, Typography, Button } from "@mui/material";
import MuiCard from "@mui/material/Card";
import { report } from "process";
import React from "react";
import { IPrpsReportCard } from "../../interfaces/IProps";
import { UpdateReport } from "../../services/ReportSvc";

const status = (status: number) => {
  switch (status) {
    case 0:
      return "Assigner";
    case 1:
      return "Proceder";
    case 2:
      return "Terminer";
    case 3:
      return "solved";
    default:
      return "unassigned";
  }
};

const Card: React.FC<IPrpsReportCard> = ({ PReport }) => {
  const styles = {
    paperContainer: {
      backgroundImage: `url(${PReport.images[0].url})`,
      height: "300px",
      width: "auto",
    },
    details: {
      color: "#fff",
    },
  };
  return (
    <div>
      <MuiCard sx={{ maxWidth: 345 }}>
        <Paper className="parent" style={styles.paperContainer}>
          <Box className="child">
            <Box className="description" sx={{ ml: 2 }}>
              <Typography variant="h6" color="white">
                {PReport.type.name}
              </Typography>
              <Typography variant="h6" color="white">
                level <b>{PReport.id} </b>
              </Typography>
              <Typography variant="h6" color="white">
                {PReport.created_at}
                {/* {typeof (report.created_at) == "string" ? report.created_at : useFormatDate(report.created_at)} */}
              </Typography>

              {PReport.state < 4 ? (
                <>
                  <button
                    onClick={() => {
                      UpdateReport(PReport);
                    }}
                  >
                    {status(PReport.state)}
                  </button>
                </>
              ) : (
                <></>
              )}
            </Box>
          </Box>
        </Paper>
      </MuiCard>
    </div>
  );
};

export default Card;
