import { Typography, Grid } from "@mui/material";
import React, { FC, useState } from "react";
import { IReport } from "../../interfaces/IReport";

import "./index.css";
import Slider from "./Silder";
interface Props {
  report: IReport;
}

const ReportModal: FC<Props> = ({ report }) => {
  // Function
  const setType = (status: number) => {
    switch (status) {
      case 0:
        return "unassigned";
      case 1:
        return "affected";
      case 2:
        return "in process";
      case 3:
        return "solved";
      default:
        return "unassigned";
    }
  };

  return (
    <div
      style={{
        display: "grid",
        gridTemplateColumns: "1fr 1fr",
        gridColumnGap: "20px",
        gridRowGap: "20px",
        justifyItems: "stretch",
        alignItems: "center",
      }}
    >
      <div
        className="scroll"
        style={{
          display: "grid",
          gridColumnGap: "20px",
          gridRowGap: "5px",
          justifyItems: "stretch",
          alignItems: "stretch",
        }}
      >
        <Typography variant="h3" color="#3a556b">
          Assingment
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>ID : </b>
          {report.id}{" "}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Sender : </b>
          {report.user.lastname + " " + report.user.firstname}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Date : </b>
          {report.created_at}
          {/* {typeof (report.created_at) == "string" ? report.created_at : useFormatDate(report.created_at)} */}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Latitude : </b>
          {report.latitude}{" "}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Longitude : </b>
          {report.longitude}{" "}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Type : </b>
          {report.type.name}{" "}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Level : </b>
          {report.level}{" "}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Status : </b>
          {setType(report.state)}
        </Typography>
        <Typography variant="subtitle1" color="#29333b">
          <b>Description : </b>
          {report.description}
        </Typography>
      </div>
      <Slider images={report.images}></Slider>
    </div>
  );
};
export default ReportModal;
